import React from 'react';
import PageTitle from '../base/pageTitle';
import WordsList from '../base/keywordsList';
import Slider from '../base/slider/slider';
import ListItem from '../base/listItem';
import imgPaths from './imgPaths';
import './balcony.css';

export default function Balcony(props) {
  //At this moment, we won't change the sequence of the images, so we can use index as key
  const imgWrap = imgPaths.map((item, index) => (
    <div key={index}>
      <img src={item} alt="" />
    </div>
  ));
  const words = [
    { id: 1, content: '' },
    { id: 2, content: '' },
    { id: 3, content: '' }
  ];
  const disadvantageItems = [
    {
      id: 1,
      content: ',,'
    },
    { id: 2, content: "'', ?" },
    { id: 3, content: '' }
  ];
  const advantageItems = [
    {
      id: 1,
      content: ','
    }
  ];
  const sliderSet = {
    dots: true,
    infinite: true,
    speed: 200,
    slidesToShow: 1,
    slidesToScroll: 1,
    className: 'balcony-slide'
  };
  return (
    <div className="balcony">
      <header>
        <PageTitle title={''} />
        <WordsList words={words} />
      </header>
      <article>
        <ListItem items={disadvantageItems} title="" />
        <ListItem items={advantageItems} title="" />
        <Slider settings={sliderSet}>{imgWrap}</Slider>
      </article>
    </div>
  );
}



